.InputBox {
    height: 75px;
    width: 339px;
    border-radius: 10px;
    border-color: #707070;
    align-self: center;
    text-align: center;
    justify-self: center;
    display: inline-flex;
    font-size: 30px;
    font-family: Arial, Helvetica, sans-serif;

    &::placeholder {
        font-size: 30px;
        font-family: Arial, Helvetica, sans-serif;
        color: rgba(36, 43, 58, 0.35);
        margin: auto 0;
    }
}