.QuestionnaireCardBill {
    margin: auto;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    border: solid red 1px;

    &__Blur {
        position: fixed;
        height: 400px;
        width: 750px;
        filter: blur(3px);
        background-color: rgba(209, 209, 209, 0.801);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    
    &__Container {
        margin: auto;
        z-index: 10;
        height: 350px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &__Top {
            display: flex;
        }

        &__Bottom {
            display: flex;
            flex-direction: column;
            margin: 100px 0 0;
            justify-content: center;
            align-content: center;
            justify-items: center;

            &__Input[type=range] {
                margin-bottom: 50px;
                width: 724px;
                -webkit-appearance: none;
                
                &::-webkit-slider-runnable-track {
                    background-color: #707070;
                    height: 10px;
                    width: 10px;
                    border-radius: 2px;
                }

                &::-webkit-slider-thumb {
                    box-shadow: 0px 0px 0px #000000;
                    border: 0px solid #000000;
                    height: 20px;
                    width: 25px;
                    border-radius: 7px;
                    background: #464646;
                    cursor: pointer;
                    -webkit-appearance: none;
                    margin-top: -3.5px;
                }

                &:focus {
                    outline: none;
                }
            }

            & > .CTA {
                margin: auto;
            }
        }
    }
        
}